document.createElement( "picture" );

(function($) {

  Formstone.Ready(function() {

    $(".js-background").background();
    $(".js-checkpoint, [data-checkpoint-animation]").checkpoint({
      offset: 100,
      reverse: true
    });
    $(".js-navigation").navigation();
    $(".js-swap").swap();

    $(".js-jump_select").on("change", function(e) {
      var $target = $(e.currentTarget);
      var url = $target.val();

      if (url) {
        window.location.href = url;
      }
    });

    $('.sticky').sticky({
      offset: 50
    });

    // $(window).on("scroll", function() {
    //   var scrolTop = $(window).scrollTop();
    //
    //   if (scrolTop > 50) {
    //     $(".header").addClass("scrolled");
    //   } else {
    //     $(".header").removeClass("scrolled");
    //   }
    // });

  });

})(jQuery);


// Scroll To

(function($) {

	var $ScrollItems;

  $(document).ready(function() {
    init();
  });

	function init() {
		$ScrollItems  = $('.js-scroll_to, a[href*="#"]').on("click", onScrollTo);
	}

  function onScrollTo(e) {
		var $target = $(e.delegateTarget),
			  id = $target[0].hash;

    var $to = $(id);

		if ($to.length) {
      e.preventDefault();
      e.stopPropagation();

      scrollToElement(id);

      $(document).trigger("scroll_to");
    }
	}

	function scrollToElement(id) {
		var $to = $(id);

		if ($to.length) {
			var offset = $to.offset();

			scrollToPosition(offset.top);
		}
	}

	function scrollToPosition(top) {
    // var offset = ($(window).width() >= 980) ? 80 : 60;
    // var offset = 65;

		$("html, body").animate({ scrollTop: top });
	}

})(jQuery);
